
*{padding: 0;margin: 0;
}
input,button,img{border: none
}
li{list-style: none
}
i{font-style: normal;
}
.read{background: #f7f7f7;padding-top: 10px;
}
.read .top{background: white;margin-bottom: 10px;
}
.read .top span{float: right;height: 70px;line-height: 70px;font-family: 'consolas';font-size: 16px;color: #5cab72;cursor: pointer;margin-right: 20px;
}
.read .top h3{text-align: left;height: 70px;line-height: 70px;font-family: '微软雅黑';font-size: 20px;color: #505050;margin-left:20px;
}
.read .top .ulbox{width: 100%;height: 200px;overflow: auto;
}
.read .top ul{height: 200px;margin-left:20px;text-align: left;float: left;width: 2000px;
}
.read .top ul li{height: 200px;float: left;margin-right: 10px;width: 100px;overflow: hidden;
}
.read .top ul li .pic{width:100px;display: block;
}
.read .top ul li h4{height: 24px;line-height: 24px;font-size: 14px;overflow: hidden;font-family: '微软雅黑';color: #323232;
}
.read .top ul li div{height: 20px;overflow: hidden;width: 100px;
}
.read .top ul li div i{height: 20px;line-height: 20px;font-size: 14px;float: left;font-family: '微软雅黑';color: #c0c0c0;
}
.read .top ul li div img{float: left;width: 15px;
}
.read .mid{width: 100%;background: white;margin-bottom: 10px;overflow: hidden;
}
.read .mid img{height: 120px;
}
.read .mid p{height: 40px;line-height: 40px;font-family: '微软雅黑';font-size: 18px;font-weight: bold;color: #3d3d3d;
}



