
*{padding: 0;margin: 0;
}
input,button{border: none
}
li{list-style: none
}
.bmain{width: 100%;overflow: hidden;padding-top: 61px;padding-bottom: 60px;
}
.bmain ul{height: 40px;width: 100%;
}
.bmain ul li{height: 40px;line-height: 40px;float: left;width: 20%;
}
a{text-decoration: none;color: #a1a1a1;
}
.bmain ul .act a{color: #6cc67e;
}
.bmain ul .act{border-bottom: 2px solid #6cc67e;
}



