
*{padding: 0;margin: 0;
}
img,button{border: none;
}
i{font-style: normal;
}
.mine .main{width: 100%;background: #f2f1ee;padding-top: 61px;padding-bottom: 61px;
}
.mine .main .top{height: 120px;background: url("../../assets/image/ic_my.jpg") no-repeat ;background-size:100%;width:100%;margin-bottom: 20px;text-align: left;
}
.mine .main .top img{width: 70px;margin-top: 25px;margin-left: 20px;float: left;
}
.mine .main .top button{height: 40px;border: 1px solid white;border-radius:5px;background: transparent;color: white;float: left;margin-top: 40px;margin-left: 50px;width: 180px;line-height: 40px;text-align: center;font-family: '微软雅黑';font-size: 16px;
}
.mine .main .mid{width: 100%;height: 110px;background:white;margin-bottom: 20px;
}
.mine .main .mid div{height: 50px;padding-top: 20px;margin-left: 15px;border-bottom: 1px solid #999999;text-align: left;
}
.mine .main .mid img{width: 35px;
}
.mine .main .mid span{line-height: 36px;vertical-align: top;margin-left: 5px;font-size: 16px;font-family:'微软雅黑';color: black;
}
.mine .main .mid i{line-height: 36px;vertical-align: top;margin-left: 5px;font-size: 26px;font-family:Consolas;color:#999999;float: right;margin-right: 20px;
}
.mine .main .mid p{line-height: 40px;vertical-align: top;margin-left: 5px;font-size: 14px;font-family:'微软雅黑';color:#111111;height: 40px;
}
.mine .main .bot{height: 120px;background: white;border-bottom:1px solid #999999;
}
.mine .main .bot li{width: 25%;float: left;padding-top: 20px;
}
.mine .main .bot li img{width: 40px;
}
.mine .main .bot li span{line-height: 40px;color: #666666;font-family:'微软雅黑';font-size: 16px;
}



