
*{padding:0;margin:0;
}
.search{width:100%;background: #42c055 url("../../assets/image/h_01.jpg") no-repeat top;height:60px;position:fixed;top:0;left:0;z-index:5;background-size: 100%;
}
.search form{padding-top:25px;width: 100%;margin-left: 10px;
}
.search form .but1{height: 30px;width: 30px;background: white url("../../assets/image/ic_search_gray.png") no-repeat center;float: left;border-radius:10px 0 0 10px;background-size: 70%
}
.search form .but2{height: 30px;width: 30px;background: white url("../../assets/image/ic_scan_gray.png") no-repeat center;float: left;border-radius:0 10px 10px 0;background-size: 70%
}
.search form .but3{height: 30px;width: 30px;background: #42c055 url("../../assets/image/ic_chat_white.png") no-repeat center;float: left;margin-left:5px;background-size: 80%
}
.search form input{width: 70%;height: 30px;background: white;line-height: 30px;font-family: '微软雅黑';font-size: 14px;color: #666666;float: left;
}
.search form input,.search form button{outline:none;
}
.search ul{position: absolute;left:10px;top:60px;z-index: 50;background: white;width: 80%;border-radius:0 0 10px 10px;
}
.search ul li{padding-top: 10px;background:white;color: #666666;border-radius: 5px;text-align: center;border-bottom: 1px solid #999999;-webkit-box-shadow: 0 3px 5px rgba(0,0,0,0.4);box-shadow: 0 3px 5px rgba(0,0,0,0.4);line-height: 20px;
}
.search ul li:hover{background: rgba(117,222,5,0.4);color: black;
}




