
*{padding: 0;margin: 0;
}
button,img{border: none;outline: none;
}
.group{width:100%
}
.group .main{padding-top: 61px;padding-bottom: 121px;
}
.group .main .m-top{height: 150px;background: url("../../assets/image/ic_group_banner.png") no-repeat center;background-size: 100%;position: relative;
}
.group .main .m-top h3{padding-top: 50px;height: 30px;line-height: 30px;font-size: 18px;
}
.group .main .m-top p{height: 30px;line-height: 30px;font-size: 16px;color: #9e9fa4;
}
.group .main .m-top span{height: 25px;line-height: 25px;font-size: 14px;color: #9e9fa4;position: absolute;left: 10px;bottom: 0;
}
.group .main .m-mid{height: 100px;background: url("../../assets/image/ic_rec_group_banner_6.png") no-repeat center;background-size:100%;width: 94%;margin-bottom: 20px;margin-left: 3%
}
.group .main .m-bot{border-top:1px solid #d4d4d4;width: 94%;position: relative;margin-left:3%;
}
.group .main .m-bot .m-bot-h31{position: absolute;top:-15px;left: 40%;width: 70px;background: white;height: 30px;line-height: 30px;font-size: 18px;text-align: center;
}
.group .main .bot{width:100%;height: 60px;background: white;-webkit-box-shadow: 0 -5px 5px rgba(0,0,0,0.1);box-shadow: 0 -5px 5px rgba(0,0,0,0.1);position: fixed;bottom: 61px;left: 0;
}
.group .main .bot button{float: right;height: 40px;width: 150px;background: #dfdfdf;margin-top: 10px;color: white;line-height: 40px;text-align: center;font-size: 16px;border-radius:5px;margin-right: 10px;cursor: pointer;
}
.group .main .bot .act{background: #42c055;
}
.group .main .m-bot .gitem{height: 60px;margin:30px 0;position: relative;
}
.group .main .m-bot .gitem .left{float: left;width: 70px;text-align: left;
}
.group .main .m-bot .gitem .left img{height: 60px;
}
.group .main .m-bot .gitem .mid{height: 60px;width: 180px;float: left;text-align: left;overflow:hidden;
}
.group .main .m-bot .gitem .mid h3{height: 30px;line-height: 30px;color: #3d3d3d;font-size: 18px;overflow: hidden;
}
.group .main .m-bot .gitem .mid p{line-height: 14px;color: #b2b2b2;font-size: 12px;
}
.group .main .m-bot .gitem .right span{float: right;color: #c9c9c9;height: 60px;line-height: 60px;font-size: 14px;margin-right: 25px;
}
.group .main .m-bot .gitem .right img{height: 20px;width: 20px;position: absolute;right: 0;top:20px;
}



