
.head{background:#fafafa url("../assets/image/isp_01.jpg") no-repeat;height: 30px;padding-top: 30px;border-bottom: 1px solid #b7b7b7;width:100%;background-size:100%;border-bottom: 1px solid #666666;position: fixed;top:0;left: 0;z-index: 50;
}
.head p{line-height: 26px;font-size: 18px;text-align: center;font-family: '微软雅黑';color:#424242;font-weight:bold;height: 26px;
}
.head .img1{position: absolute;right:60px;top: 30px;width: 25px;
}
.head .img2{position: absolute;right:10px;top: 30px;width: 25px;
}



