
*{padding:0;margin:0;
}
.hmain{padding-top: 60px;padding-bottom: 60px;
}
.hmain .swiper-slide img{height: 150px;
}
.hmain .swiper01{margin-bottom: 20px;
}
.hmain .mid h3{text-align:left;height: 28px;line-height: 28px;color: #fd8c4a;font-size: 20px;font-weight: normal;border-left:5px solid #fd8c4a;padding-left: 20px;
}
.hmain .mid .hitem{text-align: left;width: 100%;border-bottom: 1px solid #e4e4e4;padding-bottom: 10px;
}
.hmain .mid .hitem img{float: right;margin: 20px 20px 0 0;width: 100px;
}
.hmain .mid .hitem h4{margin-left: 20px;padding-top: 20px;width: 220px;line-height: 24px;font-family: '微软雅黑';font-size: 16px;color: #474747;
}
.hmain .mid .hitem p{margin-left: 20px;width: 220px;line-height: 24px;font-family: '微软雅黑';font-size: 14px;color: #a6a6a6;margin-bottom: 10px;
}
.hmain .mid .hitem span{margin-left: 20px;width: 220px;line-height: 24px;font-family: '微软雅黑';font-size: 14px;color: #a6a6a6;
}
.hmain .mid .hitem hr{margin-left: 20px;width: 20px;margin-bottom: 10px;
}



